html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: #F3F4F6;
}
body.darkmode {
  background-color: #2c2c3b;
}

.contenedor {
  transition-property: all;
  transition-duration: 300ms;
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
  position: absolute;
  margin-top: 7rem;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  padding: 2rem;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  background-color: whitesmoke;
  border-radius: 2rem;
  z-index: 20;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-transition: "color 9999s ease-out, background-color 9999s ease-out";
  -webkit-transition-delay: 9999s;
}

/** Media Queries **/
h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  text-align: center;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 2.3rem;
}
@media (min-width: 768px) {
  .tagline {
    font-size: 3rem;
  }
}

.descripcion-pagina {
  text-align: center;
}

@keyframes escala {
  100% {
    height: 1rem;
  }
}
@keyframes fadeout {
  0% {
    opacity: 1;
    transform: scale(1, 1);
  }
  100% {
    opacity: 0;
    transform: scale(1, 0.1);
  }
}
@keyframes fadein {
  0% {
    opacity: 0;
    transform: scale(1, 1);
  }
  100% {
    opacity: 1;
    transform: scale(1, 1);
  }
}
.formulario {
  margin-top: 2rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
  text-align: center;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
  border-radius: 0.5rem;
}

.acciones {
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .acciones.single {
    display: block;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
  transition-property: transform;
  transition-duration: 300ms;
}
.acciones a:hover {
  transform: scale(1.1);
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alertas .alerta-contenedor {
  margin: 1rem 0;
}
.alertas .alerta-contenedor .alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: bold;
  border-radius: 0.5rem;
  animation: 1s down-enter;
}
.alertas .alerta-contenedor .alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alertas .alerta-contenedor .alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

@keyframes down-enter {
  0% {
    clip-path: inset(0 100% 100% 0);
  }
  50% {
    clip-path: inset(0 99% 0% 0);
  }
  100% {
    clip-path: inset(0 0% 0% 0);
  }
}
@keyframes down-leave {
  0% {
    clip-path: inset(0);
  }
  100% {
    clip-path: inset(100% 0 0 0);
  }
}
@-webkit-keyframes AnimationName {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
@-moz-keyframes AnimationName {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
@keyframes AnimationName {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
@keyframes cube {
  0% {
    transform: scale(0) rotate(0deg) translate(-50%, -50%);
    opacity: 1;
  }
  100% {
    transform: scale(20) rotate(960deg) translate(-50%, -50%);
    opacity: 0;
  }
}
@keyframes cube2 {
  0% {
    transform: scale(3) rotate(0deg) translate(-50%, -50%);
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(20) rotate(960deg) translate(-50%, -50%);
    opacity: 0;
  }
}
@keyframes cube3 {
  0% {
    transform: scale(6) rotate(0deg) translate(-50%, -50%);
    opacity: 0;
  }
  10% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(20) rotate(960deg) translate(-50%, -50%);
    opacity: 0;
  }
}
@keyframes cube4 {
  0% {
    transform: scale(10) rotate(0deg) translate(-50%, -50%);
    opacity: 0;
  }
  10% {
    opacity: 0.3;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(20) rotate(960deg) translate(-50%, -50%);
    opacity: 0;
  }
}
