//Gradient
@-webkit-keyframes AnimationName {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
@-moz-keyframes AnimationName {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}
@keyframes AnimationName {
  0% {
    background-position: 0% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}

//Cube animations
@keyframes cube {
  0% {
    transform: scale(0) rotate(0deg) translate(-50%, -50%);
    opacity: 1;
  }
  100% {
    transform: scale(20) rotate(960deg) translate(-50%, -50%);
    opacity: 0;
  }
}
@keyframes cube2 {
  0% {
    transform: scale(3) rotate(0deg) translate(-50%, -50%);
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(20) rotate(960deg) translate(-50%, -50%);
    opacity: 0;
  }
}
@keyframes cube3 {
  0% {
    transform: scale(6) rotate(0deg) translate(-50%, -50%);
    opacity: 0;
  }

  10% {
    opacity: 0.3;
  }

  50% {
    opacity: 1;
  }
  100% {
    transform: scale(20) rotate(960deg) translate(-50%, -50%);
    opacity: 0;
  }
}
@keyframes cube4 {
  0% {
    transform: scale(10) rotate(0deg) translate(-50%, -50%);
    opacity: 0;
  }

  10% {
    opacity: 0.3;
  }

  50% {
    opacity: 1;
  }
  100% {
    transform: scale(20) rotate(960deg) translate(-50%, -50%);
    opacity: 0;
  }
}

.background {
  position: relative;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.background li {
  position: absolute;
  top: 80vh;
  left: 45vw;
  width: 10px;
  height: 10px;
  border: solid 1px #0039ad;
  color: transparent;
  transform-origin: top left;
  transform: scale(0) rotate(0deg) translate(-50%, -50%);
  animation: cube 10s ease-in forwards infinite;
}

.background li:nth-child(0) {
  animation: cube2 8s ease-in forwards infinite;
  left: 84vw;
  top: 68vh;
}

.background li:nth-child(1) {
  animation: cube2 13s ease-in forwards infinite;
  left: 34vw;
  top: 83vh;
}

.background li:nth-child(2) {
  left: 33vw;
  top: 57vh;
}

.background li:nth-child(3) {
  animation: cube2 15s ease-in forwards infinite;
  left: 50vw;
  top: 6vh;
}

.background li:nth-child(4) {
  animation: cube3 10s ease-in forwards infinite;
  left: 16vw;
  top: 22vh;
}

.background li:nth-child(5) {
  animation: cube2 12s ease-in forwards infinite;
  left: 92vw;
  top: 46vh;
}

.background li:nth-child(6) {
  animation: cube3 9s ease-in forwards infinite;
  left: 23vw;
  top: 30vh;
}

.background li:nth-child(7) {
  animation: cube3 14s ease-in forwards infinite;
  left: 53vw;
  top: 76vh;
}

.background li:nth-child(8) {
  animation: cube3 8s ease-in forwards infinite;
  left: 63vw;
  top: 10vh;
}

.background li:nth-child(9) {
  animation-delay: 4s;
  left: 41vw;
  top: 19vh;
}

.background li:nth-child(10) {
  animation: cube3 18s ease-in forwards infinite;
  left: 67vw;
  top: 38vh;
}

.background li:nth-child(11) {
  animation-delay: 3s;
  left: 96vw;
  top: 64vh;
}

.background li:nth-child(12) {
  animation: cube4 15s ease-in forwards infinite;
  left: 11vw;
  top: 90vh;
}

.background li:nth-child(13) {
  animation: cube3 8s ease-in forwards infinite;
  left: 76vw;
  top: 52vh;
}

