@use "base/mixins" as m;
@use "base/variables" as v;

.dashboard {
  background-color: v.$grisClaro;

  @include m.tablet {
    display: flex;
    height: 100vh; //ViewporHeight vh
  }

  .principal {
    flex: 1;
    overflow: scroll; //Scroll solo a la parte de principal
    overflow-x: hidden;
  }
  
  .contenido {
    padding: 2rem;
    .nombre-pagina {
      margin-top: 2rem;
      font-size: 2.5rem;
      text-align: center;
      text-transform: uppercase;
      color: v.$grisOscuro;
    }
  }
  
  .formulario {
    input:not([type="submit"]) {
      background-color: v.$blanco;
    }
    
    input[type="submit"] {
      @include m.boton(v.$baseazuloscuro, v.$blanco);
    }
  }
  
  .cambiar-contrase単a{
    text-align: right;
    a{
      display: inline-block;
      color: v.$baseazuloscuro;
      transition-property: transform;
      transition-duration: 300ms;
      
      &:hover{
        transform: scale(1.1);
      }
    }
  }
}

::-webkit-scrollbar {
  display: none;
}

