@use "base/variables" as v;
@use "base/mixins" as m;

.background {
  &.cyan {
    li {
      border: solid 1px v.$baseazul;
      border-color: darken(v.$baseazul, 5%);
    }
  }
}

.login {
  .uptask {
    @include m.textoDegradado(v.$cyan, v.$morado);
  }

  input[type="submit"] {
    @include m.boton(v.$baseazul, v.$blanco, 3rem);
  }

  .acciones {
    a {
      color: v.$baseazul;
    }
  }
}

.fromcyantocyan {
  @include m.gradient(v.$baseazul, v.$baseazul);
}
.frompurpletocyan {
  @include m.gradient(#d489cf, v.$baseazul);
}
.fromorangetocyan {
  @include m.gradient(#f2a58b, v.$baseazul);
}
.fromgreenyellowtocyan {
  @include m.gradient(#75ee2f, v.$baseazul);
}
.fromorangeyellowtocyan {
  @include m.gradient(#fcdc64, v.$baseazul);
}
.fromgreentocyan {
  @include m.gradient(#17d681, v.$baseazul);
}
