@use "base/mixins" as m;
@use "base/variables" as v;


.filtros {
  .filtros-inputs {
    margin-top: 2rem;

    @include m.tablet {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 2rem;
    }

    .campo {
      display: flex;
      align-items: center;

      border-radius: 100px;
      padding: 1rem;

      cursor: pointer;
      transition: 0.3s;

      &.hidden{
        display: none;
      }
    }

    .campo:hover,
    .campo:focus-within {
      background: hsla(0, 0%, 80%, 0.14);
    }

    .rad-input {
      left: 0;
      top: 0;
      width: 1px;
      height: 1px;
      opacity: 0;
      z-index: -1;
    }

    .rad-design {
      width: 22px;
      height: 22px;
      border-radius: 100px;

      background: v.$baseazuloscuro;
      position: relative;
    }

    .rad-design::before {
      content: "";

      display: inline-block;
      width: inherit;
      height: inherit;
      border-radius: inherit;

      background: hsl(0, 0%, 90%);
      transform: scale(1.1);
      transition: 0.3s;
    }

    .rad-input:checked + .rad-design::before {
      transform: scale(0);
    }

    .rad-text {
      color: v.$grisOscuro;
      margin-left: 14px;
      letter-spacing: 3px;
      text-transform: uppercase;
      font-size: 1.4rem;
      font-weight: 700;

      transition: 0.3s;
    }

    .rad-input:checked ~ .rad-text {
      color: hsl(0, 0%, 40%);
    }
  }
}
