@use 'base/mixins' as m;
@use 'base/variables' as v;


//Boton Darkmode - SunMode
body{
    .dark-mode-div{
        display: flex;
        justify-content: right;
    
        label{
            width: 6rem;
            height: 3.5rem;
            position: relative;
            display: block;
            background: #d8dadf;
            border-radius: 2rem;
            cursor: pointer;
            transition: 0.3s;
        }
        label:after{
            content: "";
            position: absolute;
            width: 2.5rem;
            height: 2.5rem;
            top: .5rem;
            left: .5rem;
            background: linear-gradient(180deg, v.$baseazul, darken(v.$baseazul, 20%));
            border-radius: 180px;
            transition: 0.3s;
        }
        
        input{
            width: 0;
            height: 0;
            visibility: hidden;
        }
        input:checked + label {
            background: #4d4d67;
        }
        input:checked + label:after{
            left:3rem;
            background: linear-gradient(180deg, v.$baseazul, darken(v.$baseazul, 20%));
        }


        label:active:after{
            transform: scale(1.2, 0.8);
        }
        
        .sun, .moon{
            position: absolute;
            width: 2rem;
            z-index: 1;
            top: .75rem;
        }
    
        .sun{
            left: .75rem;
            fill: #fff;
            transition: 0.3s;
        }
    
        svg.moon{
            left: 3.25rem;
            fill: #7e7e7e;
            transition: 0.3s;
        }
    
        input:checked + label svg.sun{
            fill: darken( #4d4d67, 20%);
        }

        input:checked + label svg.moon{
            fill: #fff;
        }
    }

    //DarkModeBTN color change
    .crear .dark-mode-div{
        label:after, input:checked + label:after{
            background: linear-gradient(180deg, #d489cf, darken(#d489cf, 20%));
        }
    }
    .olvide .dark-mode-div{
        label:after, input:checked + label:after{
            background: linear-gradient(180deg, #f2a58b, darken(#f2a58b, 20%));
        }
    }

    .dashboard .dark-mode-div{
        label:after{
            background: linear-gradient(180deg, v.$baseazul, darken(v.$baseazul, 20%))
        } 
        input:checked + label:after{
            background: linear-gradient(180deg, v.$naranja, darken(v.$naranja, 20%));
        }
    }
    
    //Skip animation if darkmode is selected on page reload
    &.skip-anim{
        .contenedor{
            transition: 0s;
        }
        .sidebar .cerrar-sesion-mobile .cerrar-sesion{
            transition: 0s;
        }
        .dark-mode-div{
            label, label:after, .sun, .moon, .contenedor{
                transition: 0s;
            }
        }
        .contenedor-nueva-tarea .agregar-tarea{
            transition: 0s;
        }
        .dashboard .formulario input[type=submit]{
            transition: 0s;
        }
    }
}

//Darkmode Changes
//Auth
.darkmode{

    $grisOscuro: #2c2c3b;

    .contenedor{
        background-color: $grisOscuro;
        box-shadow: rgba(0, 0, 0, 0.3) 0px 2px 8px 0px;
    }

    .tagline,
    .formulario label,
    .tarea p,
    .filtros .filtros-inputs .rad-text,
    .modal .formulario legend{
        color: v.$blanco;
        font-weight: 400;
    }

    .filtros .filtros-inputs .rad-input:checked~.rad-text{
        color: v.$blanco;
        font-weight: 700;
    }

    .filtros .filtros-inputs .rad-design{
        background: v.$naranja;
    }

    .descripcion-pagina, 
    .formulario .campo input,
    .contenido .nombre-pagina,
    .dashboard a{
        color: v.$grisClaro;

        .no-proyectos .boton{
            background-color: v.$naranja;
        }
    }

    .formulario .campo input,
    .proyecto,
    .modal .formulario .campo .tarea{
    background-color: lighten($grisOscuro, 15%);
    }

    .proyecto{
        &:hover{
            background-color: lighten($grisOscuro, 10%)
        }
    }

    .alertas .alerta-contenedor .alerta{
        &.error{
            background-color: darken(v.$rojo, 15%);
            color: v.$blanco;
        }

        &.exito{
            background-color: darken(v.$verde, 10%);
            color: v.$blanco;
        }
    }

    //Square background

    .background {
        &.cyan {
            li {
                border: solid 1px darken(v.$baseazul,10%);
                border-color: darken(v.$baseazul, 10%);
            }

        }
    }
    .login input[type=submit]{
        background-color: darken(#70b4f6,10%);
        &:hover{
            background-color:darken(#70b4f6,20%);
        }
    }

    .fromdarktodark{
        @include m.gradient(v.$darkMode, v.$darkMode);
    }

    //Form autofill styles
    input:-webkit-autofill,
    input:-webkit-autofill:hover,
    input:-webkit-autofill:focus,
    input:-webkit-autofill:active {
        -webkit-transition: "color 9999s ease-out, background-color 9999s ease-out";
        -webkit-transition-delay: 9999s;
    }

    //Dashboard
    .dashboard, .modal .formulario{
        background-color: $grisOscuro;
    }

    .barra-mobile, .barra, .sidebar{
        background-color: darken($grisOscuro, 10%);
        box-shadow: rgba(0, 0, 0, 0.3) 0px 2px 8px 0px;
    }
    .sidebar .sidebar-nav a{
        &.activo, &:hover{
            background-color: $grisOscuro;
        }
    }

    //Buttons
    .sidebar .cerrar-sesion-mobile .cerrar-sesion,
    .contenedor-nueva-tarea .agregar-tarea,
    .dashboard .formulario input[type=submit]{
        background-color: v.$naranja;
        &:hover{
            background-color: darken(v.$naranja, 10%);
        }
    }

    .modal .formulario .opciones .cerrar-modal{
        background-color: v.$rojo;
        &:hover{
            background-color: darken(v.$rojo, 10%);
        }
    }

    .barra p{
        color: v.$naranja;
        span{
            color: v.$blanco;
        }
    }
}