@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario{
    margin-top: 2rem;
    .campo{
        display: flex;
        align-items: center;
        margin-bottom: 2rem;

        label{
            flex: 0 0 10rem; //Ocupar un espacio fijo
            text-align: center;
        }
        
        input{
            flex: 1; //Ocupar el resto de espacio
            padding: 1rem;
            border: none;
            background-color: lighten(v.$gris, 40%);
            border-radius: .5rem;
        }
    }
}