@use "base/mixins" as m;
@use "base/variables" as v;

.background {
  &.purple {
    li {
      border: solid 1px #d489cf;
      border-color: darken(#d489cf, 5%);
    }
  }
}

.crear {
  .uptask {
    @include m.textoDegradado(v.$morado, v.$rosa);
  }

  input[type="submit"] {
    @include m.boton(#d489cf, v.$blanco, 3rem);
  }

  .acciones {
    a {
      color: #d489cf;
    }
  }
}

.fromcyantopurple {
  @include m.gradient(v.$baseazul, #d489cf);
}
.frompurpletopurple {
  @include m.gradient(#d489cf, #d489cf);
}
.fromorangetopurple {
  @include m.gradient(#f2a58b, #d489cf);
}
.fromgreenyellowtopurple {
  @include m.gradient(#75ee2f, #d489cf);
}
.fromorangeyellowtopurple {
  @include m.gradient(#fcdc64, #d489cf);
}
.fromgreentopurple {
  @include m.gradient(#17d681, #d489cf);
}
