@use "base/variables" as v;
@use "base/mixins" as m;

.background {
  &.greenyellow {
    li {
      border: solid 1px #75ee2f;
      border-color: darken(#75ee2f, 5%);
    }
  }
}

.mensaje {
  .uptask {
    @include m.textoDegradado(v.$amarillo, v.$verde);
  }

  .acciones {
    @include m.tablet {
      display: block;
    }

    a {
      color: #75ee2f;
      font-size: 2rem;
    }
  }
}

.fromcyantogreenyellow {
  @include m.gradient(v.$baseazul, #75ee2f);
}
.frompurpletogreenyellow {
  @include m.gradient(#d489cf, #75ee2f);
}
.fromorangetogreenyellow {
  @include m.gradient(#f2a58b, #75ee2f);
}
.fromgreenyellowtogreenyellow {
  @include m.gradient(#75ee2f, #75ee2f);
}
.fromorangeyellowtogreenyellow {
  @include m.gradient(#fcdc64, #75ee2f);
}
.fromgreentogreenyellow {
  @include m.gradient(#17d681, #75ee2f);
}
