@use "base/mixins" as m;
@use "base/variables" as v;

.barra-mobile{
  background-color: v.$baseazuloscuro;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include m.tablet{
    display: none;
  }

  h1{
    font-size: 3rem;
    color: v.$blanco;
    margin: 0;

    @include m.telefono{
      font-size: 5rem;
    }
  }

  .menu{
    img{
      height:3rem;
      width: 3rem;
      
      @include m.telefono{
        height:4rem;
        width: 4rem;
      }
      &:hover{
        cursor: pointer;
      }
    }
  }
}

.barra {
  display: none;


  @include m.tablet {
    background-color: v.$blanco;
    padding: 1rem 2rem;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  p {
    margin: 0;
    font-size: 1.6rem;
  }

  .cerrar-session {
    @include m.boton(v.$baseazuloscuro, v.$blanco);

    @include m.tablet {
      padding: 1rem;
      width: 15%;
      margin: 0;
      font-size: 1.4rem;
    }
  }
}
