@use "base/variables" as v;
@use "base/mixins" as m;

.background {
  &.orangeyellow {
    li {
      border: solid 1px #fcdc64;
      border-color: darken(#fcdc64, 10%);
    }
  }
}

.reestablecer {
  .uptask {
    @include m.textoDegradado(v.$naranja, v.$amarillo);
  }

  input[type="submit"] {
    @include m.boton(#fcdc64, v.$blanco, 3rem);
  }

  .acciones {
    a {
      color: #fcdc64;
    }
  }
}

.fromcyantoorangeyellow {
  @include m.gradient(v.$baseazul, #fcdc64);
}
.frompurpletoorangeyellow {
  @include m.gradient(#d489cf, #fcdc64);
}
.fromorangetoorangeyellow {
  @include m.gradient(#f2a58b, #fcdc64);
}
.fromgreenyellowtoorangeyellow {
  @include m.gradient(#75ee2f, #fcdc64);
}
.fromorangeyellowtoorangeyellow {
  @include m.gradient(#fcdc64, #fcdc64);
}
.fromgreentoorangeyellow {
  @include m.gradient(#17d681, #fcdc64);
}
