@use 'variables' as v;

*, *:before, *:after {
    box-sizing: inherit;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    font-family: v.$fuente_textos;
    font-size: 1.6rem;
    min-height: 100%;
    background-color: v.$grisClaro;

    &.darkmode{
        background-color: #2c2c3b;
    }
}
.contenedor {
    //For darkMode
    transition-property: all;
    transition-duration: 300ms;
    width: 80%;
    max-width: 1200px;
    margin: 0 auto;
    position: absolute;

    //Center position absolute element
    margin-top: 7rem;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    text-align: center;

    //Box
    padding: 2rem;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    background-color: whitesmoke;
    border-radius: 2rem;

    // //Dont slide
    // position: fixed;
    // //elements with minus z-index will be under the element
    z-index: 20;
}
.contenedor-sm {
    max-width: 70rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-transition: "color 9999s ease-out, background-color 9999s ease-out";
    -webkit-transition-delay: 9999s;
}