@use "base/variables" as v;
@use "base/mixins" as m;

.no-proyectos {
  text-align: center;

  .boton {
    @include m.boton(v.$baseazul, v.$blanco);
    @include m.tablet {
      width: 40%;
    }
  }
}

.listado-proyectos {
  list-style: none;
  padding: 1rem;
  margin-top: 2rem;

  //Small size, 1 column
  @include m.grid(1, 3rem);

  //Medium size 2 columns
  @include m.tablet {
    @include m.grid(2, 3rem);
  }

  //Big size 3 columns
  @include m.desktop {
    @include m.grid(3, 3rem);
  }

  .proyecto {
    background-color: v.$baseazuloscuro;
    min-height: 8;
    display: flex;
    align-items: center;
    
    text-align: center;
    line-height: 1.8;
    border-radius: 0.5rem;
    transition-property: transform;
    transition-duration: 300ms;

    &:hover {
      background-color: darken(v.$baseazuloscuro, 10%);
      transform: scale(1.1);
      cursor: pointer;
    }

    a {
      width: 100%;
      height: 100%;
      padding: 2rem;
      font-size: 2rem;
      margin: 0 auto;
      color: v.$blanco;
    }
  }
}
