@use 'base/mixins' as m;
@use 'base/variables' as v;

.modal{
    position: absolute;
    background-color: rgb(0 0 0 / .8);
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 20;

    .formulario{
        //Form container
        max-width: 60%;
        background-color: v.$blanco;
        padding: 2rem;
        border-radius: .5rem;
        //Center horizontally
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property:all;
        transition-duration: 600ms;
        opacity: 0;
        
        &.animar{
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        
        &.cerrar{
            transform: translateY(-200%);
            opacity: 0;
        }
        
        .opciones{
            display: flex;
            justify-content: space-between;

            .submit-nueva-tarea{
                @include m.boton(v.$baseazuloscuro, v.$blanco, 0, 0, 30%);
                margin: 0;
            }
            
            .cerrar-modal{
                @include m.boton(v.$naranja, v.$blanco, 0, 0, 30%);
                margin: 0;
            }
        }
        .campo{
            .tarea{
                background-color: #d8dadf;
            }
        }

        legend{
            margin-bottom: 1rem;
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            display: block;
        }
    }
}