@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-nueva-tarea{

    display: flex;
    justify-content: center;

    .agregar-tarea{
        @include m.boton(v.$baseazuloscuro, v.$blanco, 2rem);
        @include m.tablet{
            width: auto;
        }
    }
}

.listado-tareas{
    list-style: none;
    padding: 0;

    .no-tareas{
        text-align: center;
        margin-top: 3rem;
    }
}

.tarea{
    padding: .5rem;
    border-bottom: 2px solid darken(v.$grisClaro, 5%);
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &:last-of-type{
        border-bottom: none;
    }

    p{
        font-size: 1.8rem;
        &:hover{
            cursor: pointer;
        }
    }

    .opciones{
        display: flex;
        gap: 1rem;

        button{
            border: none;
            font-size: 1.4rem;
            padding: .5rem 1rem;
            text-transform: uppercase;
            font-weight: 700;
            border-radius: 1rem;

            &:hover{
                cursor: pointer;
            }
        }

        .completa{
            background-color: v.$baseazuloscuro;
            color: v.$blanco;
            &:hover{
                background-color: darken(v.$baseazuloscuro, 10%);
            }
        }

        .pendiente{
            background-color: v.$naranja;
            color: v.$blanco;
            &:hover{
                background-color: darken(v.$naranja, 10%);
            }
        }

        .eliminar-tarea{
            background-color: v.$rojo;
            color: v.$blanco;
            &:hover{
                background-color: darken(v.$rojo, 10%);
            }
        }
    }
}