@use 'base/variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto, $margintop: 0, $marginbottom: 0, $width: 60%) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    width: 100%;
    border-radius: 1rem;

    @include tablet {
        margin: 0 auto;
        margin-top: $margintop;
        width: $width;
    }

    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }

    &:disabled{
        opacity: 0.45; 
        cursor: not-allowed;

        &:hover{
            background-color: $bgColor;
        }
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin textoDegradado($color1, $color2){
    //Aplicar efecto fondo degradado/imagen a un texto mediandte clipping
    background: linear-gradient(to right, lighten($color1, 20%) 0%, lighten($color2, 20%) 100%);
    color: transparent;
    background-clip: text;
    
    font-size: 7rem;
    text-align: center;

    @include tablet{
        font-size: 10rem;
    }
}

@mixin gradient($color1, $color2){
    background: linear-gradient(270deg, lighten($color2, 5%), lighten($color1, 5%));
    background-size: 1200% 100%;
    -webkit-animation: AnimationName 1s linear forwards;
    -moz-animation: AnimationName 1s linear forwards;
    animation: AnimationName 1s linear forwards;
}