@use "base/mixins" as m;
@use "base/variables" as v;

.background {
  &.green {
    li {
      border: solid 1px #17d681;
      border-color: darken(#17d681, 10%);
    }
  }
}

.confirmar {
  .uptask {
    @include m.textoDegradado(v.$verde, v.$cyan);
  }

  .acciones {
    @include m.tablet {
      display: block;
    }

    a {
      color: #17d681;
      font-size: 2rem;
    }
  }
}

.fromcyantogreen {
  @include m.gradient(v.$baseazul, #17d681);
}
.frompurpletogreen {
  @include m.gradient(#d489cf, #17d681);
}
.fromorangetogreen {
  @include m.gradient(#f2a58b, #17d681);
}
.fromgreenyellowtogreen {
  @include m.gradient(#75ee2f, #17d681);
}
.fromorangeyellowtogreen {
  @include m.gradient(#fcdc64, #17d681);
}
.fromgreentogreen {
  @include m.gradient(#17d681, #17d681);
}
