@use "base/mixins" as m;
@use "base/variables" as v;

.background {
  &.orange {
    li {
      border: solid 1px #f2a58b;
      border-color: darken(#f2a58b, 5%);
    }
  }
}

.olvide {
  .uptask {
    @include m.textoDegradado(v.$rosa, v.$naranja);
  }

  input[type="submit"] {
    @include m.boton(#f2a58b, v.$blanco, 3rem);
  }

  .acciones {
    a {
      color: #f2a58b;
    }
  }
}

.fromcyantoorange {
  @include m.gradient(v.$baseazul, #f2a58b);
}
.frompurpletoorange {
  @include m.gradient(#d489cf, #f2a58b);
}
.fromorangetoorange {
  @include m.gradient(#f2a58b, #f2a58b);
}
.fromgreenyellowtoorange {
  @include m.gradient(#75ee2f, #f2a58b);
}
.fromorangeyellowtoorange {
  @include m.gradient(#fcdc64, #f2a58b);
}
.fromgreentoorange {
  @include m.gradient(#17d681, #f2a58b);
}
