@use 'base/mixins' as m;
@use 'base/variables' as v;

.acciones{
    margin-top: 4rem;
    
    @include m.tablet{
        display: flex;
        justify-content: space-between;
    }

    &.single{
        @include m.tablet{
            display: block;
        }
    }
    
    a{
        display: block;
        text-align: center;
        margin-bottom: 2rem;
        color: v.$negro;
        transition-property: transform;
        transition-duration: 300ms;

        &:hover{
            transform: scale(1.1);
        }

        &:last-of-type{
            margin-bottom: 0;
        }
    }
}