@use "base/mixins" as m;
@use "base/variables" as v;

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  opacity: 0;
  height: 0;
  overflow: hidden; //Oculto
  background-color: v.$baseazuloscuro;
  transition-property: opacity;
  transition-duration: 500ms;
  
  &.mostrar{
    display: block;
    opacity: 1;
    padding: 2rem;
    height: 100%;
    position: absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background-color: rgb(0 0 0 / .85);
    @include m.tablet{
      display: none;
    }
  }

  &.ocultar{
    opacity: 0;
  }
  
  @include m.tablet{
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: v.$baseazuloscuro;
  }

  .contenedor-sidebar{
    display: flex;
    justify-content: space-between;
    align-items: center;

    h2 {
      color: v.$blanco;
      margin: 0;
      text-align: center;
      font-size: 4rem;

      @include m.telefono{
        font-size: 5rem;
      }
    }

    .cerrar-menu{
      display: flex;
      justify-content: center;
      height: 2.5rem;
      width: 2.5rem;  

      @include m.telefono{
        height: 3rem;
        width: 3rem;  
      }

      img{
        &:hover{
          cursor: pointer;
        }
      }

      @include m.tablet{
        display: none;
      }
    }
  }
  
  .sidebar-nav {
    margin-top: 5rem;
    a {
      color: v.$blanco;
      display: block;
      font-size: 2rem;
      padding: 0.7rem;
      transition-property: 300ms;
      text-transform: uppercase;
      margin-bottom: 2rem;
      text-align: center;
      &:hover,
      &.activo {
        background-color: darken(v.$baseazul, 20%);
        border-radius: 0.5rem;
      }
      
      @include m.tablet{
        font-size: 1.6rem;
        margin-bottom: 1.5rem;
        text-align: left;
        &:hover,
        &.activo {
          background-color: darken(v.$baseazul, 20%);
          border-radius: 0.5rem;
        }
      }
    }
  }

  .cerrar-sesion-mobile{
    margin-top: 10rem;

    .cerrar-sesion{
      @include m.boton(darken(v.$baseazuloscuro, 10%), v.$blanco);
      @include m.tablet{
        margin: 0;
        width:auto;
        box-shadow: rgba(60, 64, 67, 0.4) 0px 1px 2px 0px, rgba(60, 64, 67, 0.2) 0px 1px 3px 1px;
      }
    }
  }
}
